import { call, takeLatest, put } from 'redux-saga/effects';
import { query, METHOD } from 'utils/socketApi';
import { IRequest, IParams, IBigRequest } from 'interfaces/common';
import { COMMON_SHOW_NOTIFICATION, ADD_A_COMPETITION, QUERY_ALL_COMPETITION_BY_TOURNAMENT_ID } from 'redux-saga/actions';
import { QUERY_ALL_COMPETITION_BY_TOURNAMENT_ID_SUCCESS } from 'components/CompetitionsSetting/reducers';

const addACompetition = (data: IParams, path: string | number, param: IParams) => {
  const uri = 'competition';
  const datas = { ...data };
  const paths = path;
  const params = { ...param };
  return query(uri, METHOD.POST, datas, params, paths);
};

function* doAddACompetition(request: IRequest<IBigRequest>) {
  try {
    const response = yield call(addACompetition, request.data.data, request.data.path, request.data.param);
    const data = response.data.result;
    if (response.data.error.MessageCode === 0) {
      yield put({
        type: request.response.success,
        payload: data.Competition,
      });
      yield put({
        type: QUERY_ALL_COMPETITION_BY_TOURNAMENT_ID,
        response: {
          success: QUERY_ALL_COMPETITION_BY_TOURNAMENT_ID_SUCCESS,
          failed: QUERY_ALL_COMPETITION_BY_TOURNAMENT_ID_SUCCESS,
        },
        data: {
          path: '',
          param: {
            tournamentId: request.data.data.tournamentId,
            limit: 99,
          },
          data: {},
        },
      });
    } else {
      throw new Error(response.data.error.Message);
    }
  } catch (error) {
    yield put({
      type: COMMON_SHOW_NOTIFICATION,
      data: {
        type: 'error',
        title: 'AddACompetition',
        content: error,
        time: new Date(),
      },
    });
  }
}

export default function* watchAddACompetition() {
  yield takeLatest(ADD_A_COMPETITION, doAddACompetition);
}
